/* variables */

$yellow: rgb(244, 181, 79);
$blue: rgb(43, 16, 172);
$neutral: rgb(146, 146, 138);

body {
  // disable user select text
  -webkit-user-select: none;
  // disable highlighting
  -webkit-tap-highlight-color: transparent;
  // disable callout
  -webkit-touch-callout: none;
}

.prompt {
  position: fixed;
  bottom: 0;
  width: 100vw;
  height: 24px;
  line-height: 24px;
  font-size: 14px;
  text-align: center;
  background: $yellow;
  color: #fefefe;
  z-index: -9999;
  opacity: 0;
  transition: opacity 300ms ease-out;
}
.prompt--display {
  z-index: 9999;
  opacity: 1;
}
